

import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.use(AlertPlugin)
Vue.use(ToastPlugin)

/*vuex*/
import store from './store'
/*router*/
import router from './routers'


new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})




